.main-page__section {
	padding: 40px 0;
}

.heading {
	position: relative;
	font-size: 2.1428571em;
	padding-bottom: 19px;
	line-height: 1.066;
	margin-bottom: 26px;
}

.total_content .heading {
	margin-bottom: 48px;
}

.scroll_news .heading {
	margin-bottom: 18px;
}

div>.heading:after {
	content: "";
	background: var(--c-main);
	border-radius: 4px;
	width: 75px;
	height: 6px;
	position: absolute;
	bottom: 0;
	left: 0;
}

.scroll_prices .owl-theme .owl-nav.disabled+.owl-dots {
	bottom: -30px;
}

.scroll_prices .owl-stage-outer {
	padding: 10px;
	margin: -10px;
}

.prices-section__dropdown-tab-panel {
	position: relative;
	display: block;
	width: 100%;
	padding: 15px 55px 15px 25px;
	border: 1px solid #eff1f5;
	margin: 0 0 -1px;
	cursor: pointer;
	color: #212121;
	background: #fff;
}

.prices-section__dropdown-tab-panel.collapsed:after {
	border-bottom: 0px;
	border-top: 3px solid #212121;
	border-left: 4px solid transparent;
	border-right: 4px solid transparent;
}

.prices-section__dropdown-tab-panel:after {
	content: "";
	position: absolute;
	right: 25px;
	top: 50%;
	width: 0px;
	height: 0px;
	margin: -2px 0 0;
	border-bottom: 3px solid #212121;
	border-top: 0px;
	border-left: 4px solid transparent;
	border-right: 4px solid transparent;
}

.prices-section__dropdown-tab-panel:focus,
.video-section__dropdown-tab-panel:active {
	color: #000;
	text-decoration: none;
}

.price-list__item {
	border-radius: 12px;
	box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.14);
	background-color: #fff;
	overflow: hidden;
	display: flex;
	transition: all .2s ease;
}

.price-list__item:hover {
	box-shadow: 0 0 12px 2px rgba(0, 0, 0, 0.14);
}

.price-list__col:nth-child(n+3) {
	margin-top: 30px;
}

.price-list__left,
.price-list__right {
	width: 50%;
}

.price-list__right {
	padding: 20px;
}

.price-list__title {
	font-weight: 600;
	font-size: 14px;
}

.price-list__img {
	max-height: 250px;
	overflow: hidden;
}

.price-list__img img {
	height: 100%;
	width: auto;
}

.price-list__list {
	margin-top: 10px;
	padding: 0 0 0 16px;
	margin-bottom: rem(10);
}

.price-list__list-item {
	margin: 0;
	padding: 0;

	list-style: disc;
}

.price-list__buttons {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.price-list__button {
	background-color: var(--c-main);
	padding: 10px 30px;
	color: #fff;
	border-radius: 6px;
	display: flex;
	align-items: center;
}

.price-list__button svg {
	margin-left: 5px;
}

.price-list__button:hover,
.price-list__button:focus,
.price-list__button:active {
	color: #fff;
	opacity: .8;
	text-decoration: none;
}

.price-list__sale {
	text-decoration: line-through;
}

@media (max-width:992px) {
	.price-list__col:nth-child(n+2) {
		margin-top: 30px;
	}
}

@media(max-width: 768px) {
	.price-list__col {
		padding: 0 !important;
	}
}

@media (max-width:480px) {
	.price-list__item {
		flex-direction: column;
	}

	.price-list__left,
	.price-list__right {
		width: 100%;
	}

	.price-list__img {
		width: 100%;
		height: auto;
		overflow: hidden;
		max-height: 250px;
	}

	.price-list__img img {
		width: 100%;
		height: auto;
	}
}


.scroll_prices #price_list_owl {
	margin-bottom: 0;
}

.scroll_prices .projects-section__container {
	padding-bottom: 0px;
}

.nav.projects-section__tabs-list {
	margin-bottom: 40px;
	border-bottom: 1px solid #E1E1E1;
}

.projects-section__tabs-list>li {
	margin-right: 6px;
}

.projects-section__tabs-list>li>a.active {
	color: var(--c-main);
}

.projects-section__tabs-list>li>a {
	padding: 9px 26px;
	font-size: 1em;
	color: #212121;
	display: block;
	text-decoration: none;
	position: relative;
}

.projects-section__tabs-list>li>a:after {
	content: '';
	position: absolute;
	left: 0;
	right: 0;
	bottom: -1px;
	height: 2px;
	width: 100%;
	background: transparent;
	z-index: 2;
}

.projects-section__tabs-list>li:hover>a:after,
.projects-section__tabs-list>li>a.active:after {
	background: var(--c-main);
}

