  font-family: 'Comfortaa';
  src: local("Comfortaa Regular"), local("Comfortaa-Regular"), url("../fonts/comfortaa/Comfortaa-Regular.woff2") format("woff2"), url("../fonts/comfortaa/Comfortaa-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Comfortaa';
  src: local("Comfortaa Bold"), local("Comfortaa-Bold"), url("../fonts/comfortaa/Comfortaa-Bold.woff2") format("woff2"), url("../fonts/comfortaa/Comfortaa-Bold.woff") format("woff");
  font-weight: 700;
