:root {
  --w-container: 1344px;
  --c-main: #21A7AC;
  --c-help: #0A5998;
  --c-blue-gradient: linear-gradient(-45deg, #6a46ff, #3202f9);
  --c-text: #212121;
}

* {
  padding: 0;
  margin: 0;
  outline: none;
  border: 0;
}

html,
body {
  height: 100%;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

button {
  background-color: transparent;
  margin: 0;
}

body {
  font-family: 'Montserrat';
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
}

.container {
  max-width: var(--w-container) !important;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.relative {
  position: relative;
}

.w-8\/12 {
  flex: 0 0 66.666667%;
  max-width: 66.666667%;
}

.w-7\/12 {
  flex: 0 0 58.333333%;
  max-width: 58.333333%;
}

.w-6\/12 {
  flex: 0 0 50%;
  max-width: 50%;
}

.w-5\/12 {
  flex: 0 0 41.666667%;
  max-width: 41.666667%;
}

.w-4\/12 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.block {
  display: block;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-col {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.flex-center {
  justify-content: center;
}

.flex-between {
  justify-content: space-between;
}

.items-center {
  align-items: center !important;
}

.items-start {
  align-items: flex-start !important;
}

.items-end {
  align-items: flex-end !important;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.header {
  background-color: #fff;
  width: 100%;
  z-index: 100;
  position: relative;
}

.header_top {
  padding: 1.25rem 0;
}

.header_top-wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.header_top-wrap .burger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  width: 3.25rem;
  height: 0.875rem;
  margin-right: 1.25rem;
}

.header_top-wrap .burger:hover span, .header_top-wrap .burger:hover::before, .header_top-wrap .burger:hover::after {
  background-color: var(--c-main);
}

.header_top-wrap .burger span, .header_top-wrap .burger::before, .header_top-wrap .burger::after {
  height: 0.125rem;
  background-color: var(--c-text);
}

.header_top-wrap .burger::before, .header_top-wrap .burger::after {
  content: "";
}

.header_top-wrap .burger::before {
  width: 1.1875rem;
}

.header_top-wrap .burger::after {
  width: 0.75rem;
}

.header_top-wrap .burger span {
  width: 1rem;
}

.header_top-logo {
  margin-right: 6.625rem;
  margin-top: 0.375rem;
}

@media (max-width: 899px) {
  .header_top-logo {
    margin-right: 1.25rem;
  }
}

@media (max-width: 540px) {
  .header_top-logo {
    margin-right: auto;
  }
}

.header_top-phone {
  position: relative;
  margin-right: 4rem;
}

@media (max-width: 899px) {
  .header_top-phone {
    display: none;
  }
}

.header_top-phone .top:hover .header_top-phone_drop {
  display: block;
}

.header_top-phone_drop {
  position: absolute;
  top: 1.375rem;
  right: 0;
  background-color: white;
  padding: 0.5rem 0;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.1875rem;
  display: none;
}

.header_top-phone_drop a {
  display: block;
  white-space: nowrap;
  font-size: 0.875rem;
  font-weight: 400;
  padding: 0.25rem 1rem;
  transition: all 0.3s ease;
}

.header_top-phone_drop a:hover {
  background-color: rgba(0, 0, 0, 0.15);
}

.header_top-phone .top {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header_top-phone .top > a {
  font-size: 1.0625rem;
  font-weight: 600;
  color: var(--c-text);
}

.header_top-phone .top .arrow-bottom {
  margin-left: 0.3125rem;
}

.header_top-phone .bottom a {
  font-size: 0.625rem;
  text-transform: uppercase;
  transition: .3s;
}

.header_top-phone .bottom a:hover {
  color: var(--c-main);
}

.header_top-text {
  display: flex;
  flex-direction: column;
  margin-right: auto;
}

@media (max-width: 1050px) {
  .header_top-text {
    display: none;
  }
}

.header_top-right {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header_top-right .change_sity {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

@media (max-width: 540px) {
  .header_top-right .change_sity {
    display: none;
  }
}

.header_top-right .change_sity:hover .change_sity-drop {
  display: block;
}

.header_top-right .change_sity-drop {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: #fff;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.15);
  display: none;
}

.header_top-right .change_sity-drop ul {
  display: flex;
  flex-direction: column;
}

.header_top-right .change_sity-drop ul li a {
  white-space: nowrap;
  display: block;
  padding: 0.625rem 1.25rem;
  color: var(--c-text);
  transition: .3s;
}

.header_top-right .change_sity-drop ul li a:hover {
  opacity: .8;
}

.header_top-right .change_sity .fa-map-marker-alt {
  color: var(--c-help);
  margin-right: 0.3125rem;
}

.header_top-right .change_sity span {
  font-size: 1rem;
  border-bottom: 1px dashed var(--c-main);
  margin-right: 0.3125rem;
  transition: 0.3s;
  font-weight: 500;
}

.header_top-right .search,
.header_top-right .cart_shop,
.header_top-right .account_block {
  margin-left: 1.5rem;
}

.header_top-right .cart_shop {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header_top-right .cart_shop-count {
  margin-left: 0.3125rem;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  background-color: var(--c-main);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  color: white;
}

.header_bottom {
  background-color: var(--c-main);
}

@media (max-width: 1050px) {
  .header_bottom {
    display: none;
  }
}

.header_bottom-nav ul {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.header_bottom-nav ul li a {
  transition: all 0.3s ease;
  display: block;
  padding: 1rem 0.9375rem;
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
  color: white;
}

.header_bottom-nav ul li a:hover {
  opacity: .8;
}

.header_search {
  position: static;
}

.header_search-wrap {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 993;
  transition: .6s;
  background: #fff;
  top: -100px;
  visibility: hidden;
  opacity: 0;
  transition: top 0.3s, visibility 1s, opacity 1s;
}

.search_field {
  color: #444;
  display: block;
  line-height: 18px;
  vertical-align: top;
  width: 100%;
  font-size: 1.8667em;
  height: 132px;
  background: 0;
  border: 0;
  border-radius: 0;
  padding: 0 128px 0 0;
}

.search_form {
  padding-right: 60px;
  position: relative;
}

.search-close {
  position: absolute;
  right: 0;
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 48px;
  cursor: pointer;
}

/* header */
.btn {
  background: var(--c-help);
  border: 0 solid var(--c-help);
  border-radius: 4px;
  padding: 14px 26px;
  color: white;
  font-size: 1em;
  transition: .3s;
  line-height: normal;
}

.btn:not(:disabled):not(.disabled) {
  cursor: pointer;
}

.arrow-bottom {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-top: 6px dashed;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  border-radius: 10px;
  color: var(--c-main);
}

.start_page {
  padding: 3.125rem 0;
  background: url(../img/header_1.jpg) top center;
  background-size: cover;
  margin-bottom: 2.625rem;
}

.start_page .container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.start_page .container .link {
  position: absolute;
  top: 0;
  left: 1.25rem;
}

.start_page .container .link a {
  position: relative;
  margin-right: 0.875rem;
}

.start_page .container .link a:hover {
  color: var(--c-main);
  opacity: 1;
}

.start_page .container .link a::before {
  position: absolute;
  top: 0;
  right: -0.625rem;
  content: "/";
  pointer-events: none;
}

.start_page .container .link a:last-child {
  margin-right: 0;
}

.start_page .container .link a, .start_page .container .link span {
  font-size: 0.875rem;
  opacity: .5;
}

.start_page__right {
  width: 31.25rem;
}

@media (max-width: 1050px) {
  .start_page__right {
    width: 100%;
    margin-top: 2.5rem;
    margin-bottom: 1.25rem;
  }
}

.start_page__left {
  background: rgba(245, 245, 245, 0.7);
  padding: 1.5625rem;
  width: 32.375rem;
}

@media (max-width: 1050px) {
  .start_page__left {
    width: 100%;
    margin-top: 2.5rem;
    margin-bottom: 1.25rem;
  }
}

.start_page__left h1 {
  font-size: 3.125rem;
  line-height: 3.625rem;
  color: var(--c-text);
}

@media (max-width: 1050px) {
  .start_page__left h1 {
    font-size: 1.75rem;
    line-height: 2.125rem;
  }
}

.start_page__left p {
  margin: 1.5rem 0 0.625rem;
  font-size: 1.0625rem;
}

@media (max-width: 1050px) {
  .start_page__left p {
    font-size: 0.9375rem;
  }
}

.advantages-sect {
  padding: 40px 0;
  background-color: #dce2fa;
}

.advantage-cnt {
  min-height: 60px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.adv-icon-cnt {
  width: 80px;
  margin-right: 20px;
}

.adv-icon-cnt img {
  max-width: 100%;
  max-height: 100%;
}

.adv-text-cnt {
  text-align: left;
  color: #212121;
  width: 100%;
}

.advantage-block:nth-child(n+6) {
  padding-top: 40px;
}

.prices-section__tab-panel > .row {
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 991px) {
  .advantage-cnt {
    margin-bottom: 40px;
  }
  .all-advantages .col-*:last-child .advantage-cnt {
    margin-bottom: 0px;
  }
}

@media (max-width: 767px) {
  .advantage-cnt {
    display: block;
  }
  .adv-icon-cnt {
    margin-bottom: 8px;
  }
  #advantages.section_grey {
    padding: 60px 0 20px;
  }
}

.main-page__section {
  padding: 40px 0;
}

.heading {
  position: relative;
  font-size: 2.1428571em;
  padding-bottom: 19px;
  line-height: 1.066;
  margin-bottom: 26px;
}

.total_content .heading {
  margin-bottom: 48px;
}

.scroll_news .heading {
  margin-bottom: 18px;
}

div > .heading:after {
  content: "";
  background: var(--c-main);
  border-radius: 4px;
  width: 75px;
  height: 6px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.scroll_prices .owl-theme .owl-nav.disabled + .owl-dots {
  bottom: -30px;
}

.scroll_prices .owl-stage-outer {
  padding: 10px;
  margin: -10px;
}

.prices-section__dropdown-tab-panel {
  position: relative;
  display: block;
  width: 100%;
  padding: 15px 55px 15px 25px;
  border: 1px solid #eff1f5;
  margin: 0 0 -1px;
  cursor: pointer;
  color: #212121;
  background: #fff;
}

.prices-section__dropdown-tab-panel.collapsed:after {
  border-bottom: 0px;
  border-top: 3px solid #212121;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
}

.prices-section__dropdown-tab-panel:after {
  content: "";
  position: absolute;
  right: 25px;
  top: 50%;
  width: 0px;
  height: 0px;
  margin: -2px 0 0;
  border-bottom: 3px solid #212121;
  border-top: 0px;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
}

.prices-section__dropdown-tab-panel:focus,
.video-section__dropdown-tab-panel:active {
  color: #000;
  text-decoration: none;
}

.price-list__item {
  border-radius: 12px;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.14);
  background-color: #fff;
  overflow: hidden;
  display: flex;
  transition: all .2s ease;
}

.price-list__item:hover {
  box-shadow: 0 0 12px 2px rgba(0, 0, 0, 0.14);
}

.price-list__col:nth-child(n+3) {
  margin-top: 30px;
}

.price-list__left,
.price-list__right {
  width: 50%;
}

.price-list__right {
  padding: 20px;
}

.price-list__title {
  font-weight: 600;
  font-size: 14px;
}

.price-list__img {
  max-height: 250px;
  overflow: hidden;
}

.price-list__img img {
  height: 100%;
  width: auto;
}

.price-list__list {
  margin-top: 10px;
  padding: 0 0 0 16px;
  margin-bottom: 0.625rem;
}

.price-list__list-item {
  margin: 0;
  padding: 0;
  list-style: disc;
}

.price-list__buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.price-list__button {
  background-color: var(--c-main);
  padding: 10px 30px;
  color: #fff;
  border-radius: 6px;
  display: flex;
  align-items: center;
}

.price-list__button svg {
  margin-left: 5px;
}

.price-list__button:hover,
.price-list__button:focus,
.price-list__button:active {
  color: #fff;
  opacity: .8;
  text-decoration: none;
}

.price-list__sale {
  text-decoration: line-through;
}

@media (max-width: 992px) {
  .price-list__col:nth-child(n+2) {
    margin-top: 30px;
  }
}

@media (max-width: 768px) {
  .price-list__col {
    padding: 0 !important;
  }
}

@media (max-width: 480px) {
  .price-list__item {
    flex-direction: column;
  }
  .price-list__left,
  .price-list__right {
    width: 100%;
  }
  .price-list__img {
    width: 100%;
    height: auto;
    overflow: hidden;
    max-height: 250px;
  }
  .price-list__img img {
    width: 100%;
    height: auto;
  }
}

.scroll_prices #price_list_owl {
  margin-bottom: 0;
}

.scroll_prices .projects-section__container {
  padding-bottom: 0px;
}

.nav.projects-section__tabs-list {
  margin-bottom: 40px;
  border-bottom: 1px solid #E1E1E1;
}

.projects-section__tabs-list > li {
  margin-right: 6px;
}

.projects-section__tabs-list > li > a.active {
  color: var(--c-main);
}

.projects-section__tabs-list > li > a {
  padding: 9px 26px;
  font-size: 1em;
  color: #212121;
  display: block;
  text-decoration: none;
  position: relative;
}

.projects-section__tabs-list > li > a:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
  height: 2px;
  width: 100%;
  background: transparent;
  z-index: 2;
}

.projects-section__tabs-list > li:hover > a:after,
.projects-section__tabs-list > li > a.active:after {
  background: var(--c-main);
}

.projects-section__container-content {
  display: flex;
  flex-wrap: wrap;
}

.projects-section__container-content .projects-item {
  width: calc((100% - 4rem) / 3);
  margin-right: 2rem;
}

@media (max-width: 899px) {
  .projects-section__container-content .projects-item {
    width: 100%;
    margin-right: 0;
  }
}

.projects-section__container-content .projects-item:last-child {
  margin-right: 0;
}

.projects-section__container-content .projects-item__image {
  border-radius: 0.3125rem;
  height: 237px;
  display: block;
  position: relative;
  overflow: hidden;
