  }
}

@media (min-width: 992px) {
  .bottom-nav-list__root {
    font-size: 1em;
  }
  .bottom-nav-list__submenu > li > a {
    font-size: 1.1428em;
    line-height: 1.2857em;
  }
  .bottom-nav-list__section {
    margin: 0;
  }
  .bottom-nav-list__root {
    margin-bottom: 20px;
  }
  .bottom-nav-list__submenu > li > a {
