.advantages-sect {
	padding: 40px 0;
	background-color: #dce2fa;
}

.advantage-cnt {
	min-height: 60px;
	display: flex;
	align-items: center;
	justify-content: flex-start;
}

.adv-icon-cnt {
	width: 80px;
	margin-right: 20px;
}

.adv-icon-cnt img {
	max-width: 100%;
	max-height: 100%;
}

.adv-text-cnt {
	text-align: left;
	color: #212121;
	width: 100%;
}

.advantage-block:nth-child(n+6) {
	padding-top: 40px;
}

.prices-section__tab-panel > .row {
	margin-left: auto;
	margin-right: auto;
}

@media (max-width: 991px) {
	.advantage-cnt {
		margin-bottom: 40px;
	}

	.all-advantages .col-*:last-child .advantage-cnt {
		margin-bottom: 0px;
	}
}

@media (max-width: 767px) {
	.advantage-cnt {
		display: block;
	}

	.adv-icon-cnt {
		margin-bottom: 8px;
	}

	#advantages.section_grey {
		padding: 60px 0 20px;
	}
}