:root {
	--w-container: 1344px;
	--c-main: #21A7AC;
	--c-help: #0A5998;
	--c-blue-gradient: linear-gradient(-45deg, #6a46ff, #3202f9);

	--c-text: #212121;
}

* {
	padding: 0;
	margin: 0;
	outline: none;
	border: 0;
}

html,
body {
	height: 100%;
}

svg {
	overflow: hidden;
	vertical-align: middle;
}

button {
	background-color: transparent;
	margin: 0;
}

body {
	font-family: 'Montserrat';
	font-size: 14px;

	font-weight: 400;
	line-height: 1.5;
	color: #212529;
	background-color: #fff;
}

.container {
	max-width: var(--w-container) !important;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}