.calc-content {
	display: block;
	border: 0.5rem solid var(--c-main);
	border-radius: 0.5rem;
	padding: 1rem;
	background-color: #fafafa;
	max-width: 500px;
	width: 100%;
	text-align: left;
	color: #000;
	box-sizing: border-box;

	p {
		margin-bottom: 10px;
	}
}

.calc-content .header b {
	font-size: 1.2rem;
}

.content-select {
	display: flex;
	margin-top: 1rem;
}

.content-select>div {
	width: 50%;
}

.form-elem {
	margin-bottom: 0.5rem;
}

.radio {
	display: inline-block;

	input {
		display: none;
	}
}

.radio>input~span {
	display: inline;
	font-weight: 400;
}

.radio>input:checked~span {
	font-weight: 600;
}

.radio>input+div {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 1.5rem;
	height: 1.5rem;
	border-radius: 50%;
	border: 1px solid #ccc;
	margin-right: 0.5rem;
	box-sizing: border-box;
	transition: 0.2s border-color ease;
}
.radio>input:checked+div {
	border: 3px solid #555;
}

.radio>input+div::before {
	content: ' ';
	position: absolute;
	width: 0.7rem;
	height: 0.7rem;
	border-radius: 50%;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	background-color: #555;
	opacity: 0.0;
}

.radio>input:checked+div::before {
	opacity: 1;
}

.content-range {
	display: flex;
	align-items: center;
}

.num-input {
	position: relative;
}

.num-input>input {
	width: 6rem;
	padding: 0.25rem;
	padding-left: 0.5rem;
	border-radius: 0.25rem;
	border: 1px solid #ccc;
	font-size: 1.5rem;
	font-weight: 700;
}

.num-input>span {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 1.5rem;
}

.range-input {
	flex-grow: 1;
	margin-left: 1rem;
}

input[type=range] {
	height: rem(8);
	border-radius: 3px;
	-webkit-appearance: none;
	width: 100%;
	background-color: rgba(0,0,0,.35);
}

input[type=range]:focus {
	outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
	width: 100%;
	height: 0.4rem;
	cursor: pointer;
	background: #525c2e;
	border-radius: 0.2rem;
}

input[type=range]::-webkit-slider-thumb {
	box-shadow: 0px 0px 0px #000031;
	border: 0.3rem solid #ddd;
	height: 1.5rem;
	width: 1.5rem;
	border-radius: 50%;
	background: #FFFFFF;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -0.6rem;
	box-shadow: 0px 0px 4px #666a;
}

input[type=range]:focus::-webkit-slider-runnable-track {
	background: #525c2e;
}

input[type=range]::-moz-range-track {
	width: 100%;
	height: 0.4rem;
	cursor: pointer;
	background: #525c2e;
	/*#525c2e #EADC06*/
	border-radius: 0.2rem;
}

input[type=range]::-moz-range-thumb {
	border: 0.3rem solid #ddd;
	height: 1rem;
	width: 1rem;
	border-radius: 50%;
	background: #FFFFFF;
	cursor: pointer;
	box-shadow: 0px 0px 4px #666a;
}

input[type=range]::-ms-track {
	width: 100%;
	height: 0.4rem;
	cursor: pointer;
	background: transparent;
	border-color: transparent;
	color: transparent;
}

input[type=range]::-ms-fill-lower {
	background: #EADC06;
	border-radius: 0.2rem;
}

input[type=range]::-ms-fill-upper {
	background: #EADC06;
	border-radius: 0.2rem;
}

input[type=range]::-ms-thumb {
	margin-top: 1px;
	border: 0.3rem solid #ddd;
	height: 1.5rem;
	width: 1.5rem;
	border-radius: 50%;
	background: #FFFFFF;
	cursor: pointer;
	box-shadow: 0px 0px 4px #666a;
}

input[type=range]:focus::-ms-fill-lower {
	background: #EADC06;
}

input[type=range]:focus::-ms-fill-upper {
	background: #EADC06;
}

.range-input>input {
	width: 100%;
}

.checkbox {
	display: inline-block;
}

.checkbox>input {
	display: none;
}

.checkbox>input+div {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 1rem;
	height: 1rem;
	border: 1px solid #ccc;
	border-radius: 3px;
	margin-right: 0.5rem;
	background-size: contain;
	box-sizing: border-box;
	transition: 0.2s background-color ease;
	background-color: #FFFFFF;
}

.checkbox>input~span {
	display: inline;
	font-weight: 400;
}
.checkbox>input:checked+div {
	background-color: #EADC06;
}
.form-input {
	position: relative;
	border-radius: .5rem;
	border: 1px solid #dcdcdc;
	box-shadow: inset 1px 1px 0 #a8a8a8;
	padding: 1rem 1rem 1rem 3rem;
	background-size: 1.25rem 1.25rem;
	background-position: .25rem center;
	background-repeat: no-repeat;
}

.last-content {
	width: 100%;
	text-align: center;
	margin-top: 1.5rem;
	margin-bottom: 0.5rem;
}

.shine-button {
	position: relative;
	overflow: hidden;
	vertical-align: middle;
	border: 0 solid #ffd131;
	font-size: .875rem;
	outline: 0;
	color: #524329;
	letter-spacing: .1em;
	font-weight: 700;
	padding: 0 2.8125rem;
	height: 4.375rem;
	line-height: 4.375rem;
	border-radius: 5px;
	text-shadow: 0 2px 1px rgba(255,255, 255, .43);
	box-shadow: 0 1.1875rem 1.375rem rgba(109, 89, 21, .39), inset 0 0.25rem 0.3125rem rgba(255, 255, 255, .33), inset 0 0 0 4.375rem rgba(255, 225, 125, 0);
	background: linear-gradient(350deg, #f5b812 0, #ffdb5e 100%);
	transition: all .1s linear;
}

.shine-button::before {
	text-decoration: none;
	position: absolute;
	top: 0;
	left: 0;
	content: '';
	width: 100%;
	height: 100%;
	background: linear-gradient(110deg, rgba(255, 255, 255, 0) 15%, rgba(255, 255, 255, .75) 25%, rgba(255, 255, 255, 0) 35%);
	animation: shine 2.5s linear infinite;
}

.anim-underline::after {
	content: " ";
	position: absolute;
	left: 0;
	bottom: 0;
	height: 1px;
	width: 100%;
	background-color: var(--color-black);
	transform-origin: center 0 0;
	transform: scaleX(0);
}

.anim-underline::after,
.colored-link,
.dashed-link,
.dialog-common-style,
.flick-button,
.menu-button-line {
	transition: all .3s ease;
}

@keyframes shine {
	0% {
		opacity: .1;
		transform: translate(-50%, 0)
	}

	16% {
		opacity: 1;
		transform: translate(35%, 0)
	}

	22% {
		opacity: .5;
		transform: translate(55%, 0)
	}

	40% {
		transform: translate(120%, 0)
	}

	100% {
		opacity: .35;
		transform: translate(120%, 0)
	}
}