.header {
	background-color: #fff;
	width: 100%;
	z-index: 100;
	position: relative;

	&_top {
		padding: rem(20) 0;

		&-wrap {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			.burger {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				cursor: pointer;
				width: rem(52);
				height: rem(14);
				margin-right: rem(20);

				&:hover {
					span, &::before, &::after {
						background-color: var(--c-main);
					}
				}

				span, &::before, &::after {
					height: rem(2);
					background-color: var(--c-text);
				}
				
				&::before, &::after {
					content: "";
				}

				&::before {
					width: rem(19);
				}

				&::after {
					width: rem(12);
				}

				span {
					width: rem(16);
				}
			}
		}

		&-logo {
			margin-right: rem(106);

			margin-top: rem(6);

			@include min {
				margin-right: rem(20);
			}

			@include min540 {
				margin-right: auto;
			}
		}

		&-phone {
			position: relative;
			margin-right: rem(64);

			@include min {
				display: none;
			}

			.top:hover {
				.header_top-phone_drop {
					display: block;
				}
			}

			&_drop {
				position: absolute;
				top: rem(22);
				right: 0;
				background-color: white;
				padding: rem(8) 0;
				border: 1px solid rgba(0, 0, 0, .15);
				border-radius: rem(3);

				display: none;

				a {
					display: block;
					white-space: nowrap;
					font-size: rem(14);
					font-weight: 400;
					padding: rem(4) rem(16);

					transition: all 0.3s ease;

					&:hover {
						background-color: rgba(0, 0, 0, .15);
					}
				}
			}

			.top {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				> a {
					font-size: rem(17);
					font-weight: 600;
					color: var(--c-text);

					
				}

				.arrow-bottom {
					margin-left: rem(5);
				}
			}

			.bottom {
				a {
					font-size: rem(10);
					text-transform: uppercase;
					transition: .3s;
					&:hover {
						color: var(--c-main);
					}
				}
			}
		}

		&-text {
			display: flex;
			flex-direction: column;

			margin-right: auto;

			@include min1050 {
				display: none;
			}

			span {

			}
		}

		&-right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.change_sity {
				position: relative;
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				@include min540 {
					display: none;
				}

				&:hover {
					.change_sity-drop {
						display: block;
					}
				}

				&-drop {
					position: absolute;
					top: 100%;
					right: 0;
					background-color: #fff;
					box-shadow: 0 5px 5px 0 rgba(0,0,0,.15);
					display: none;
					ul {
						display: flex;
						flex-direction: column;

						li {

							a {
								white-space: nowrap;
								display: block;
								padding: rem(10) rem(20);
								color: var(--c-text);
								transition: .3s;
								&:hover {
									opacity: .8;
								}
							}
						}
					}
				}

				.fa-map-marker-alt {
					color: var(--c-help);
					margin-right: rem(5);
				}

				span {
					font-size: rem(16);
					border-bottom: 1px dashed var(--c-main);
					margin-right: rem(5);
					transition: 0.3s;
					font-weight: 500;
				}
			}

			.search,
			.cart_shop,
			.account_block {
				margin-left: rem(24);
			}

			.search {
				// margin-left: rem(25);
				
			}
			.cart_shop {
				// margin-left: rem(25);

				display: flex;
				flex-wrap: wrap;
				align-items: center;

				&-count {
					margin-left: rem(5);

					width: rem(20);
					height: rem(20);
					border-radius: 50%;
					background-color: var(--c-main);

					display: flex;
					align-items: center;
					justify-content: center;

					font-size: rem(12);
					color: white;
				}
			}
		}
	}

	&_bottom {
		background-color: var(--c-main);

		@include min1050 {
			display: none;
		}

		&-nav {
			ul {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: space-between;

				li {

					a {
						transition: all 0.3s ease;
						display: block;
						padding: rem(16) rem(15);

						font-size: rem(12);
						font-weight: bold;
						text-transform: uppercase;
						color: white;

						&:hover {
							opacity: .8;
						}
					}
				}
			}
		}
	}
}

.header_search {
	position: static;
}

.header_search-wrap {
	position: absolute;
	left: 0;
	right: 0;
	z-index: 993;
	transition: .6s;
	background: #fff;
	top: -100px;
	visibility: hidden;
	opacity: 0;
	transition: top 0.3s, visibility 1s, opacity 1s;
}

.search_field {
	color: #444;
	display: block;
	line-height: 18px;
	vertical-align: top;
	width: 100%;
	font-size: 1.8667em;
	height: 132px;
	background: 0;
	border: 0;
	border-radius: 0;
	padding: 0 128px 0 0;
}

.search_form {
	padding-right: 60px;
	position: relative;
}

.search-close {
	position: absolute;
	right: 0;
	width: 35px;
	height: 35px;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 48px;
	cursor: pointer;
}

.search-input {}

/* header */


.btn {
	background: var(--c-help);
	border: 0 solid var(--c-help);
	border-radius: 4px;
	padding: 14px 26px;
	color: white;
	font-size: 1em;
	transition: .3s;
	line-height: normal;
}

.btn:not(:disabled):not(.disabled) {
	cursor: pointer;
}


.arrow-bottom {
	display: inline-block;
	width: 0;
	height: 0;
	margin-left: 2px;
	vertical-align: middle;
	border-top: 6px dashed;
	border-right: 6px solid transparent;
	border-left: 6px solid transparent;
	border-radius: 10px;

	color: var(--c-main);
}