.uk-table {
	border-collapse: collapse;
	border-spacing: 0;
	width: 100%;
	margin-bottom: 15px;
}

.table-price {
	border: 1px solid #e6e6e6;
	border-collapse: collapse;
	margin-bottom: 0;

	td, th {
		border: 1px solid #e6e6e6;
		// text-align: center;
		color: #1e1e1e;
		font-weight: 600;
		font-size: 16px;
		line-height: 1.2;
		padding: 20px;
	}

	&__title {
		text-align: left !important;
		background-color: var(--c-main);
		color: white !important;
		font-size: 24px !important;
		line-height: 1.24;
		padding: 15px 20px;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}

	&__numb {
		width: 50px;
	}

	&__name {
		text-align: left;
	}

	&__unit {
		width: 75px;
		padding: 20px 0;
	}

	&__amount {
		width: 105px;
		padding: 20px;

		input {
			width: 100px;
			height: 40px;
			background-color: whitesmoke;
			border: 1px solid #e6e6e6;
			border-radius: 4px;
			text-align: center;
		}
	}

	&__price {
		width: 70px;
		padding: 10px 0;
	}

	&__sum {
		width: 120px;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		padding: 20px 0;
	}
}

.total {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	min-height: 60px;
	background-color: #1e1e1e;
	padding: 10px 20px;

	&__title {
		color: white;
		font-size: 24px;
		font-weight: 700;
		flex-grow: 1;
	}

	&__sum {
		color: white;
		font-size: 16px;
		display: block;
		margin: 0 40px;
	}

	&__reset {
		width: 102px;
		height: 40px;
		background-color: white;
		border-radius: 4px;
		color: #1e1e1e;
		font-size: 14px;
		text-align: center;
		line-height: 40px;
		padding: 0 25px;
		margin: 0 auto;
	}
}

.block-form {
	text-align: center;
	border: 10px solid #1e1e1e;
	padding: 70px 50px 50px;
}

.table-price__total-box {
	background-color: #fafafa;
	box-shadow: 0 30px 60px rgba(30, 30, 30, .2);
	border: 0;
	text-align: left;
}

.form-box__title {
	color: #1e1e1e;
	font-size: 30px;
	line-height: 1;
	font-weight: 700;
	margin-bottom: 45px;
	text-align: center;
}


.uk-form input,
.uk-form select,
.uk-form textarea {
	box-sizing: border-box;
	margin: 0;
	border-radius: 0;
	font: inherit;
	color: inherit;
	box-shadow: inset 0 3px 0 #e6e6e6;
}

.block-form .form-base input {
	display: block;
	width: 100%;
}

.uk-form input:not([type="radio"]):not([type="checkbox"]),
.uk-form select {
	vertical-align: middle;
}

.uk-grid {
	display: flex;
	flex-wrap: wrap;
	margin: 0;
	padding: 0;
	list-style: none;
}

.uk-grid {
	margin-left: -25px;
}

@media (min-width: 1220px) {
	.uk-grid {
		margin-left: -30px;
	}
}

.uk-form> :last-child {
	margin-bottom: 0;
}

.uk-grid:before,
.uk-grid:after {
	content: "";
	display: block;
	overflow: hidden;
}

.uk-grid>* {
	flex: none;
	margin: 0;
	float: left;
}

.uk-grid>* {
	padding-left: 25px;
}

@media (min-width: 1220px) {
	.uk-grid>* {
		padding-left: 30px;
	}
}

[class*='uk-width'] {
	box-sizing: border-box;
	width: 100%;
}

@media (min-width: 768px) {
	.uk-width-medium-1-3,
	.uk-width-medium-2-6 {
		width: 33.333%;
	}
}

@media (min-width: 768px) {
	.uk-width-medium-1-1 {
		width: 100%;
	}
}

.uk-form-icon {
	display: inline-block;
	position: relative;
	max-width: 100%;
}

.uk-form-icon {
	display: block;
	text-align: center;
	margin-bottom: 10px;
}

.table-price__total-box .uk-form-icon {
	max-width: 100% !important;
	margin: 0 auto;
}

.block-form .form-base .uk-form-icon {
	max-width: 350px;
	margin: 0 auto;
}

.uk-form-row:after {
	clear: both;
}

.uk-form-row:before,
.uk-form-row:after {
	content: "";
	display: table;
}

.uk-form select,
.uk-form textarea,
.uk-form input:not([type]),
.uk-form input[type="text"],
.uk-form input[type="password"],
.uk-form input[type="datetime"],
.uk-form input[type="datetime-local"],
.uk-form input[type="date"],
.uk-form input[type="month"],
.uk-form input[type="time"],
.uk-form input[type="week"],
.uk-form input[type="number"],
.uk-form input[type="email"],
.uk-form input[type="url"],
.uk-form input[type="search"],
.uk-form input[type="tel"],
.uk-form input[type="color"] {
	height: 60px;
	max-width: 100%;
	padding: 0 23px;
	border: 1px solid #e6e6e6;
	background: #f5f5f5;
	color: #282828;
	-webkit-transition: all 0.2s linear;
	-webkit-transition-property: border, background, color, box-shadow, padding;
	transition: all 0.2s linear;
	transition-property: border, background, color, box-shadow, padding;
	border-radius: 6px;
}

.table-price__total-box textarea {
	height: 135px;
	width: 100%;
	max-width: 100%;
	padding: 20px 23px 20px 45px;
	border: 1px solid #e6e6e6;
	background: #f5f5f5;
	color: #282828;
	-webkit-transition: all 0.2s linear;
	-webkit-transition-property: border, background, color, box-shadow, padding;
	-o-transition: all 0.2s linear;
	transition: all 0.2s linear;
	-webkit-transition-property: border, background, color, padding, -webkit-box-shadow;
	transition-property: border, background, color, padding, -webkit-box-shadow;
	-o-transition-property: border, background, color, box-shadow, padding;
	transition-property: border, background, color, box-shadow, padding;
	transition-property: border, background, color, box-shadow, padding, -webkit-box-shadow;
	border-radius: 6px;
	resize: none;
}

.uk-form-icon:not(.uk-form-icon-flip)>input {
	padding-left: 50px !important;
}

.block-form .form-base input {
	display: block;
	width: 100%;
}

.uk-form input:not([type="radio"]):not([type="checkbox"]),
.uk-form select {
	vertical-align: middle;
}

.form-box__total {
	text-align: center;
	background-image: url(../img/table-price__total.png);
	background-repeat: no-repeat;
	background-size: contain;
	max-width: 210px;
	height: 123px;
	margin: 0 auto;
}

.form-box__total--title {
	color: white;
	font-size: 24px;
	padding-top: 15px;
	margin-bottom: 5px;
}

.form-box__total--price {
	color: white;
	font-size: 24px;
	font-weight: 700;
	text-transform: uppercase;
}

.form-box--btn {
	display: inline-block;
	margin-top: 20px;
}

.btn-big {
	height: 70px;
	color: #ffffff;
	font-size: 17px;
	line-height: 70px;
	font-weight: 700;
	text-transform: uppercase;
	text-shadow: 0 1px 0 rgba(203, 88, 39, .75);
	border: 0;
	border-bottom: 6px solid var(--c-help);
	padding: 0 40px;
	border-radius: 6px;
	background-color: var(--c-help);
}

.form-box--text {
	display: inline-block;
	margin-top: 20px;
	margin-left: 40px;
}

.form-box--text p {
	display: inline-block;
}

.form-box--text-link {
	display: inline-block;
	margin-left: 10px;
	color: #07D;
}

#table-price-form .js-step-2 {
	display: none;
	padding: 5vh 0;
	text-align: center;
}

#table-price-form .js-step-2 .uk-icon {
	font-size: 100px;
}

.uk-form-row+.uk-form-row {
	margin-top: 15px;
}

.uk-form-icon>[class*='uk-icon-'] {
	position: absolute;
	top: 50%;
	width: 30px;
	margin-top: -7px;
	font-size: 14px;
	color: #999;
	text-align: center;
	pointer-events: none;
}

.table-price__total-box i.uk-icon.uk-icon-comment {
	top: 30px;
	left: 10px;
}

.uk-form-icon>[class*='uk-icon-'] {
	left: 15px;
}

@media only screen and (max-width: 767px) {
	.table-price__total-box i.uk-icon.uk-icon-comment {
		top: 50px;
		// left: 10px;
	}
	.table-price {
		border: 1px solid #e6e6e6;
	}
	.table-price .table-price__head {
		display: none;
	}
	.table-price td {
		width: 100% !important;
		display: block;
		text-align: right;
		position: relative;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		padding: 10px;
		border-bottom: 1px solid rgba(241, 241, 241, 0.5);
		padding-left: 120px;
	}

	.table-price .table-price__name {
		text-align: right !important;
	}

	.table-price .table-price__name {
		font-size: 14px;
	}

	.table-price .table-price__unit {
		padding: 10px;
	}

	.table-price .table-price__sum {
		padding: 10px;
		height: 40px;
	}

	.table-price .table-price__title {
		padding: 10px 15px;
		vertical-align: middle;
	}

	.table-price .table-price__row td:before {
		content: attr(data-title);
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 120px;
		height: 100%;
		background-color: #e6e6e6;
		text-align: left;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		font-size: 12px;
		font-weight: 700;
		padding: 10px 5px 5px 10px;
	}

	.table-price .table-price__name {
		font-size: 12px;
	}

	.total__title {
		font-size: 16px;
	}

	.table-price__total-box textarea {
		margin-top: 20px;
	}

	.form-box__total {
		margin-top: 20px;
	}

	.form-box--text {
		text-align: center;
		display: block;
		margin: 0;
	}

	.form-box--btn {
		display: table;
		margin: 20px auto;
	}
}

@media only screen and (max-width: 1000px) {
	.form-box--text p {
		display: block;
		margin-bottom: 0;
	}
	.form-box--text-link {
		display: block;
		margin-left: 0;
		text-align: center;
	}
	
}

@media only screen and (max-width: 1180px) {
	.block-form {
		padding: 35px 25px 25px;
	}
}

@media only screen and (max-width: 959px) {
	.btn-big {
		font-size: 16px;
		padding: 15px 30px;
		height: auto;
		line-height: normal;
	}
}