.start_page {
	// height: rem(633);
	padding: rem(50) 0;
	background: url(../img/header_1.jpg) top center;
	background-size: cover;

	margin-bottom: rem(42);

	.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		height: 100%;

		.link {
			position: absolute;
			top: 0;
			left: rem(20);

			a {
				position: relative;
				margin-right: rem(14);

				&:hover {
					color: var(--c-main);
					opacity: 1;
				}

				&::before {
					position: absolute;
					top: 0;
					right: rem(-10);
					content: "/";
					pointer-events: none;
				}

				&:last-child {
					margin-right: 0;
					
				}
			}

			a, span {
				font-size: rem(14);

				opacity: .5;
			}
		}
	}

	&__right {
		width: rem(500);

		@include min1050 {
			width: 100%;
			margin-top: rem(40);
			margin-bottom: rem(20);
		}
	}

	&__left {
		// position: relative;
		background: rgba(245, 245, 245, 0.7);
		padding: rem(25);

		width: rem(518);

		@include min1050 {
			width: 100%;
			margin-top: rem(40);
			margin-bottom: rem(20);
		}

		h1 {
			font-size: rem(50);
			line-height: rem(58);
			color: var(--c-text);

			@include min1050 {
				font-size: rem(28);
				line-height: rem(34);
			}
		}

		p {
			margin: rem(24) 0 rem(10);
			font-size: rem(17);

			@include min1050 {
				font-size: rem(15);
			}
		}
	}
}