.popup_overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.45);
	z-index: 100;
	// display: flex;
	justify-content: center;
	align-items: center;

	overflow-y: auto;

	display: none;

	// padding: 100px 0;
}

.popup_overlay.active {
	display: flex;
}

.popup {
	position: relative;
	width: rem(600);
	padding: rem(30) rem(70);
	background-color: white;

	@include min768 {
		margin-left: 20px;
		margin-right: 20px;
		padding: rem(20);
	}

	&_close {
		position: absolute;
		top: rem(-34);
		right: rem(-34);

		@include min768 {
			top: rem(5);
			right: rem(5);
		}

		i {
			color: white;
			font-size: rem(32);
			
			@include min768 {
				color: var(--c-help);
			}
		}
	}

	&_header {
		text-align: center;
		margin-bottom: rem(40);

		@include min660 {
			margin-bottom: rem(20);
		}

		h3 {
			font-size: rem(42);
			margin-bottom: rem(10);

			@include min660 {
				font-size: rem(26);
			}
		}
	}

	&_form {
		margin-bottom: rem(30);

		@include min660 {
			margin-bottom: rem(15);
		}

		.field-set {
			position: relative;
			margin-bottom: rem(20);

			@include min660 {
				margin-bottom: rem(14);
			}

			i {
				position: absolute;
				left: 15px;
				top: 50%;
				width: 30px;
				margin-top: -7px;
				font-size: 14px;
				color: #999;
				text-align: center;
				pointer-events: none;
			}

			.field {
				width: 100%;
				height: 60px;
				max-width: 100%;
				padding: 0 23px;
				border: 1px solid #e6e6e6;
				background: #f5f5f5;
				color: #282828;
				-webkit-transition: all 0.2s linear;
				-webkit-transition-property: border,
				background,
				color,
				box-shadow,
				padding;
				transition: all 0.2s linear;
				transition-property: border,
				background,
				color,
				box-shadow,
				padding;
				border-radius: 6px;

				padding-left: 50px;
			}
		}
	}

	&_bottom {
		text-align: center;

		p {
			margin-bottom: rem(14);
		}

		p, a {
			font-size: rem(12);
		}

		a {
			color: var(--c-help);
		}
	}
}