.item {
	border-radius: rem(3);
}

@media(max-width: 1200px) {
	.total_content .scroll_promotions {
		padding-bottom: 30px;
	}
}

@media(max-width: 768px) {
	.total_content .scroll_promotions {
		padding-bottom: 40px;
	}
}

.scroll_promotions .owl-theme .owl-nav.disabled+.owl-dots {
	margin-top: 30px;
	position: relative;
}

.promotions-promotion {
	display: flex;
	flex-wrap: wrap;

	.item {
		width: calc((100% - 4rem) / 4);
		margin-right: rem(20);
		margin-bottom: rem(20);

		@include min {
			width: calc((100% - 1.3rem) / 2);

			&:nth-child(2n) {
				margin-right: 0;
			}
		}

		&:last-child {
			margin-right: 0;
		}
	}
}

.promotions-promotion>.container {
	position: relative;
}

.sect_promotion_cnt {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	position: relative;
	min-height: 322px;
	padding: 20px;
	position: relative;
	transition: .5s;
	border-radius: 15px;
	text-decoration: none;
	overflow: hidden;
	transform: scale(1);
}

.sect_promotion_cnt:hover {
	/* box-shadow: 0 0 32px #777; */
	transition: .5s;
	text-decoration: none;
}

.sect_promotion_cnt * {
	position: relative;
	z-index: 2;
}

.sect_promotion_cnt:hover>.sect_promotion_bg {
	transform: scale(1.1);
}

.sect_promotion_cnt .sect_promotion_bg {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 1;
	border-radius: 15px;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}

.sect_promotion_cnt .sect_promotion_bg-shadow {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: linear-gradient(to top, rgba(0, 0, 0, 0.3), rgba(255, 255, 255, 0));
}

.sect_promotion_block {
	margin-bottom: 30px;
}

.name_data {
	margin-top: auto;
}

.sect_promotion_heading {
	font-size: 14px;
	color: #fff;
	font-weight: bold;
	line-height: normal;
}

.sect_promotion_btn {
	position: absolute;
	bottom: 70px;
	left: 50px;
	color: #fff !important;
}

.sect_promotion_dates {
	color: #fff;
	font-size: 1em;
	line-height: 1.375;
	margin-top: 6px;
	margin-bottom: 15px;
	background: var(--prv_interactive_color);
	display: inline-block;
	padding: 5px 10px;
}

.sect_promotion_discount {
	position: absolute;
	right: 20px;
	top: 20px;
	padding: 3px 15px;
	color: #fff;
	background-color: #81CE41;
	border-radius: 40px;
	font-size: 1.0814285em;
	font-weight: bold;
	text-align: center;
}

@media(max-width:767px) {
	.sect_promotion_btn {
		left: 20px;
	}

	.sect_promotion_cnt {
		padding: 93px 20px 111px 20px;
		height: 100%;
	}
}