.projects-section__container-content {
	display: flex;
	flex-wrap: wrap;

	.projects-item {
		width: calc((100% - 4rem) / 3);
		margin-right: rem(32);

		@include min {
			width: 100%;
			margin-right: 0;
		}

		

		&:last-child {
			margin-right: 0;
		}

		&__image {
			border-radius: rem(5);
			
			height: 237px;
			display: block;
			position: relative;
			overflow: hidden;

			@include min {
				height: auto;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}

		&__descr {
			background-color: #F8F9FB;
			padding: 20px 25px;
			display: flex;
			flex-direction: column;
		}

		&__title {
			font-weight: bold;
			font-size: 16px;
			text-align: center;
			margin-bottom: 25px;
		}

		&__list {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			@include min {
				justify-content: center;
			}

			&-item {
				display: flex;
				flex-direction: column;
				align-items: center;

				@include min {
					margin-right: rem(14);

					&:last-child {
						margin-right: 0;
					}
				}

				img {
					margin-bottom: rem(8);
				}

				.text {
					font-weight: bold;
					font-size: 12px;
				}
			}
		}
	}
}

.wallpaper-slider {
	position: relative;
	width: 100%;
	height: 100%;
}

.scroll_projects .owl-theme .owl-nav.disabled {
	margin-top: 30px;
}

.scroll_projects .owl-theme.wallpaper-slider .owl-nav.disabled+.owl-dots {
	width: 100%;
	display: flex;
	justify-content: space-between;
	position: absolute;
	top: 0;
	height: 100%;
	margin-top: 0 !important;
}

.scroll_projects .owl-theme.wallpaper-slider .owl-dots .owl-dot {
	display: flex;
	align-items: flex-end;
	height: 100%;
	width: 25%;
	margin: 0px 3px;
}

.scroll_projects .owl-theme.wallpaper-slider .owl-dots .owl-dot span {
	background: rgba(255, 255, 255, 0.8);
	width: 100%;
	height: 2px;
	margin: 0 0 20px 0;
	border-radius: 0;
}

.scroll_projects .owl-theme.wallpaper-slider .owl-dots .owl-dot.active span {
	background: var(--c-main);
	width: 100%;
	height: 2px;
	margin: 0 0 20px 0;
	border-radius: 0;
}